<template>
  <div id="meslist">
    <div class="tableHeader">
      <p>发布时间</p>
      <p>发布平台</p>
      <p>用户名称</p>
      <p>私信标题</p>
      <p>创建原因</p>
      <p>状态</p>
    </div>
    <div class="divideLine"></div>
    <div class="emptyDiv"></div>
    <div class="content2">
      <order-list :list="listShow"></order-list>
      <div class="paginationZone" v-show="listShow.length !== 0">
        <a-pagination
          :total="listAll.length"
          :show-total="
            (total, range) => `${range[0]}-${range[1]} of ${total} items`
          "
          :page-size="pageSize"
          :default-current="1"
          v-model:current="currentPage"
        />
      </div>
      <empty-data-placeholder
        v-if="listShow.length === 0"
        description="暂无工单数据"
      ></empty-data-placeholder>
    </div>

    <div class="fixedCoverPage" v-if="isShowCoverPage"></div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
  <div class="bottom">
    <div class="button b1" @click="refresh(10000)">全平台</div>
    <div class="button b2" @click="refresh(10001)">iOS</div>
    <div class="button b3" @click="refresh(10002)">微信小程序</div>
    <div class="button b4" @click="showAddDialog">发布私信</div>
  </div>

  <div class="add-toast" v-if="ifShowAdd">
    <add-mes-user
      class="add-mes-user"
      @hideDialog="hideAddDialog"
    ></add-mes-user>
  </div>
</template>
<script>
import EmptyDataPlaceholder from "@/pages/internal/components/common/emptyDataPlaceholder/EmptyDataPlaceholder";
import OrderList from "@/pages/internal/views/adminTools/privateMes/chilcomponents/orderList/OrderList";
import AddMesUser from "@/pages/internal/views/adminTools/privateMes/chilcomponents/addMesUser/AddMesUser";
import { $getUserMessage } from "@/pages/internal/network/adminTools/message";
export default {
  data() {
    return {
      listAll: [],
      listShow: [],
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
      isShowCoverPage: false,
      showIndex: -1,
      x: null,
      y: null,
      ifShowAdd: false,
      isLoading: false,
    };
  },
  async mounted() {
    let data = {
      is_active: true,
      platform: 10000,
    };
    this.isLoading = true;
    try {
      const response = await $getUserMessage(data);
      console.log(response);
      const { Code, msg } = response;
      // 获取当前登录用户所有工单数据
      if (Code === 200 && msg === "success") {
        console.log(response.details.notifyusers);
        this.listAll = response.details.notifyusers.reverse(); // 将数据赋值给listAll
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
        const tempListAll = window.JSON.parse(
          window.JSON.stringify(this.listAll)
        );
        // 若listShow数组为空，则说明是首次加载组件
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize);
        }
        // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          );
        }
      } else {
        this.$toast.error("获取工单数据失败 ", 240);
        console.log("获取工单数据失败 ");
      }
    } catch (err) {
      this.$toast.error("获取工单数据失败  ", 240);
      console.log("获取工单数据失败 ");
    }
    this.isLoading = false;
  },
  methods: {
    showAddDialog() {
      this.ifShowAdd = true;
    },
    hideAddDialog() {
      this.ifShowAdd = false;
    },
    async refresh(platform) {
      let data = {
        is_active: true,
        platform: platform,
      };
      this.isLoading = true;
      try {
        this.currentPage = 1;
        const response = await $getUserMessage(data);
        console.log(response);
        const { Code, msg } = response;
        // 获取当前登录用户所有工单数据
        if (Code === 200 && msg === "success") {
          console.log(response.details.notifyusers);
          this.listAll = response.details.notifyusers.reverse(); // 将数据赋值给listAll
          this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
          const tempListAll = window.JSON.parse(
            window.JSON.stringify(this.listAll)
          );
          this.listShow = tempListAll.slice(0, this.pageSize);
          this.currentPage = 1;
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          );
        } else {
          this.$toast.error("获取工单数据失败 ", 240);
          console.log("获取工单数据失败 ");
        }
      } catch (err) {
        this.$toast.error("获取工单数据失败  ", 240);
        console.log("获取工单数据失败 ");
      }
      this.$refs.scroll.scrollTo(0, 0, 400);
      setTimeout(() => {
        this.$refs.scroll.refresh();
      }, 200);
      this.isLoading = false;
    },
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(
        window.JSON.stringify(this.listAll)
      );
      this.listShow = tempListAll.slice(
        this.pageSize * (newVal - 1),
        this.pageSize * newVal
      );
      this.$refs.scroll.scrollTo(0, 0, 400);
      setTimeout(() => {
        this.$refs.scroll.refresh();
      }, 200);
    },
  },
  components: {
    EmptyDataPlaceholder,
    OrderList,
    AddMesUser,
  },
};
</script>
