<template>
  <div class="addMesProvider">
    <div class="top">
      <div class="cancel" @click="hideDialog"></div>
    </div>
    <div class="title">发布私信</div>
    <div class="form">
      <div class="form-item">
        <label for="identity">接收人身份</label>
        <input
          type="text"
          id="identity"
          class="form-item-content"
          value="用户"
          ref="identity"
          readonly="readonly"
        />
      </div>
      <div class="form-item">
        <label for="platform">发布平台</label>
        <select v-model="monitoredMixedObj.platform" id="platform">
          <option value="10000">全平台</option>
          <option value="10001">IOS</option>
          <option value="10002">微信小程序</option>
        </select>
      </div>
      <div class="form-item">
        <label for="user-id">服务商ID</label>
        <input
          type="text"
          placeholder="请输入服务商ID"
          id="user-id"
          class="form-item-content"
          v-model="monitoredMixedObj.user_id"
          ref="recipient_id"
        />
      </div>
      <div class="form-item">
        <label for="title">私信标题</label>
        <input
          type="text"
          placeholder="请输入标题"
          id="title"
          class="form-item-content"
          v-model="monitoredMixedObj.title"
          ref="title"
        />
      </div>
      <div class="form-item">
        <label for="content">私信内容</label>
        <textarea
          id="content"
          placeholder="  请输入内容"
          class="form-item-content"
          v-model="monitoredMixedObj.content"
          ref="content"
        ></textarea>
      </div>
      <div class="form-item">
        <label for="reason">创建原因</label>
        <input
          type="text"
          placeholder="请输入内容"
          id="reason"
          class="form-item-content"
          v-model="monitoredMixedObj.reason"
          ref="reason"
        />
      </div>
      <div class="form-item">
        <label for="status">私信状态</label>
        <select v-model="monitoredMixedObj.status" id="status">
          <option value="12003">普通</option>
          <option value="12000">紧急</option>
        </select>
      </div>

      <div class="buttonZone">
        <button class="cancelBtn other-button" @click="hideDialog">取消</button>
        <button class="ensureBtn submit-button" @click="checkInput">
          确认
        </button>
      </div>

      <div class="edit-reason" v-show="ifShowEditReason">
        <edit-reason-dialog
          @cancelClick="hideEditReason"
          @ensureClick="submit"
        ></edit-reason-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import EditReasonDialog from "@/pages/internal/components/content/adminTools/editReasonDialog/EditReasonDialog";
import { $submitUserMessge } from "@/pages/internal/network/adminTools/message";

export default {
  name: "e-AddMesProvider",
  data() {
    return {
      monitoredMixedObj: {
        user_id: "",
        platform: "10000",
        status: "12003",
        title: "",
        reason: "",
        content: "",
      },
      ifShowEditReason: false,
    };
  },
  methods: {
    hideDialog() {
      this.$emit("hideDialog");
    },
    checkInput() {
      if (this.monitoredMixedObj.user_id === "") {
        this.$toast.error("请填写用户ID", 240);
        return;
      }
      if (this.monitoredMixedObj.title === "") {
        this.$toast.error("请填写标题", 240);
        return;
      }
      if (this.monitoredMixedObj.reason === "") {
        this.$toast.error("请填写创建理由", 240);
        return;
      }
      if (this.monitoredMixedObj.content === "") {
        this.$toast.error("请填写内容", 240);
        return;
      }
      this.showEditReason();
    },
    showEditReason() {
      this.ifShowEditReason = true;
    },
    hideEditReason() {
      this.ifShowEditReason = false;
    },
    async submit(edit_reason) {
      this.hideEditReason();
      let data = {
        user: {
          id: window.localStorage.getItem("userID"), // user_id 当前登录管理端账号的user id
        },
        version: "1.0.0",
        create_reason: edit_reason,
        work_line_id: 1900, // CATEGORY = 1200, FEATURE = 1300, PRODUCT = 1400, PROVIDER = 1500, NOTIFY_GENERAL = 1600， CUSTOMER_COMMENT = 1700，PROVIDER_INSURANCE_INFO = 1800, NOTIFY_USER = 1900
        work_operation: 4200, // CREATE = 4200, UPDATE = 4300
        content: {
          send_id: window.localStorage.getItem("userID"), // user_id 当前登录管理端账号的user id, 和上面的“id”一样
          recipient_id: this.monitoredMixedObj.user_id, //和通用公告不一样的地方 // 收信用户的user id
          platform: this.monitoredMixedObj.platform, // ALL_Mobile = 10000, IOS = 10001, MINIAPP = 10002
          create_reason: this.monitoredMixedObj.reason,
          title: this.monitoredMixedObj.title,
          status: this.monitoredMixedObj.status, // URGENT = 12000， INACTIVE = 12002， UNREAD = 12003， READED = 12004 【创建使用12003】
          content: {
            text: this.monitoredMixedObj.content,
          },
        },
      };
      console.log(data);
      try {
        const response = await $submitUserMessge(data);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("提交成功", 240);
        } else {
          this.$toast.error("提交失败", 240);
        }
      } catch (e) {
        this.$toast.error("提交失败", 240);
      }
    },
  },
  watch: {},
  components: {
    EditReasonDialog,
  },
};
</script>

