export function $getProviderMessge(requestPayload) {
    return request({
        method: 'post',
        url: '/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/notify/user/all',
        data: window.JSON.stringify(requestPayload),
        auth: {
            username: 'NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4',
            password: 'NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366',
        },
        headers: {
            'Content-Type': 'application/json',
        },
    })
}

