<template>
  <div class="list-item">
    <div class="order-content">
      <div>{{ setDate(item.date_created) }}</div>
      <div>{{ platform[item.platform] }}</div>
      <div>{{ item.title }}</div>
      <div>{{ item.recipient_id }}</div>
      <div>{{ item.create_reason }}</div>
      <div>
        <div class="status">{{ status[item.status] }}</div>
        <!--          <a-tooltip>-->
        <!--            <template #title>点击查看详情</template>-->
        <!--            <div class="check" @click="viewDetailsClick">查看</div>-->
        <!--          </a-tooltip>-->
      </div>
    </div>
  </div>
</template>

<script>
import { UrlDecode, processDate } from "../../../../../../common/utils";
import { status, platform } from "@/pages/internal/views/adminTools/transform";

export default {
  name: "e-MesListItem",
  props: {
    item: {
      type: Object,
      default() {
        return {};
      },
    },
    index: {
      type: Number,
    },
  },
  data() {
    return {
      platform,
      status,
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    viewDetailsClick(e) {
      const param = {
        x: e.clientX,
        y: e.clientY,
        index: this.index,
      };
      this.$bus.$emit("viewDetailsClick", param);
    },
    setStatus(code) {
      if (code === 3200) {
        return "待审核";
      }
    },
    setModuleName(code) {
      if (code === 1200) {
        return "热门类别";
      }
    },
    setDate(text) {
      return processDate(text);
    },
  },
  components: {},
};
</script>

