<template>
  <div class="editReasonDialogWrapper">
    <div class="edit-reason-dialog">
      <p class="title">请填写编辑理由</p>
      <p class="cancel" @click="cancelClick"></p>
      <div class="textAreaDiv">
        <textarea v-model="message" ref="textarea" />
      </div>
      <div class="buttonDiv">
        <button class="cancel other-button" @click="cancelClick">取消</button>
        <button class="submit other-button" @click="ensureClick">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-EditReasonDialog",
  data() {
    return {
      message: "",
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.textarea.focus();
    });
  },
  methods: {
    ensureClick() {
      if (this.message === "") {
        this.$toast.error("请填写理由", 240);
        return;
      }
      this.$emit("ensureClick", this.message);
    },
    cancelClick() {
      this.$emit("cancelClick");
    },
  },
};
</script>

